<template>
    <div id = "white">
        <div class = "container ">
            <div class = "row mt justify-content-center">
                <div class = "col-lg-4 col-lg-offset-4 centered">
                    <h3>Contact us</h3>
                    <hr>
                </div>
            </div>
            <div class = "row justify-content-center">
                <div class = "col-md-8 col-md-offset-2">

                    <form class = "contact-form php-mail-form" role = "form" action = "" method = "">

                        <div class = "form-group">
                            <input type = "name" name = "name" class = "form-control" id = "contact-name"
                                   placeholder = "Your name" data-rule = "minlen:4"
                                   data-msg = "Please enter at least 4 chars" v-model = "message.cp_name">
                            <div class = "validate"></div>
                        </div>
                        <div class = "form-group">
                            <input type = "email" name = "email" class = "form-control" id = "contact-email"
                                   placeholder = "Your contact information" data-rule = "email" data-msg = "Please enter a valid email"
                                   v-model = "message.mobile">
                            <div class = "validate"></div>
                        </div>
                        <div class = "form-group">
                            <input type = "text" name = "subject" class = "form-control" id = "contact-subject"
                                   placeholder = "Title" data-rule = "minlen:4"
                                   data-msg = "Please enter at least 8 chars of subject" v-model = "message.title">
                            <div class = "validate"></div>
                        </div>

                        <div class = "form-group">
                            <textarea class = "form-control" name = "message" id = "contact-message"
                                      placeholder = "Please enter the content" rows = "5" data-rule = "required"
                                      data-msg = "Please write something for us" v-model = "message.content"></textarea>
                            <div class = "validate"></div>
                        </div>

                        <div class = "loading"></div>
                        <div class = "error-message"></div>

                        <div class = "form-send">
                            <button type = "button" class = "btn btn-large" @click = "check">Contact Us</button>
                        </div>
                    </form>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "comment",
        data: () => ({
            message: {}
        }),
        methods: {
            check(){
                let _self = this;
                if(_self.message.cp_name === undefined || _self.message.cp_name === ''){
                    _self.$message({
                        message: 'Please enter your name',
                        type: 'warning'
                    });
                    return;
                }
                //
                if(_self.message.mobile === undefined ||  _self.message.mobile === ''){
                    _self.$message({
                        message: 'Please enter your contact information',
                        type: 'warning'
                    });
                    return;
                }
                //
                if(_self.message.title === undefined ||  _self.message.title === ''){
                    _self.$message({
                        message: 'Please enter your subject',
                        type: 'warning'
                    });
                    return;

                }
                //
                if(_self.message.content === undefined ||  _self.message.content === ''){
                    _self.$message({
                        message: 'Please enter your content',
                        type: 'warning'
                    });
                    return;

                }
                _self.sendMessage();
            },
            sendMessage() {
                let _self = this;
                _self.message['type'] = 1;
                _self.$ajax.post('/join/join', _self.message)
                    .then(res => {
                        if (res.resultCode === 1) {
                            _self.$message({
                                message: `${res.resultMsg}`,
                                type: 'success'
                            });
                            _self.message = {}
                        } else {
                            _self.$message({
                                message: `${res.resultMsg},请联系管理员！`,
                                type: 'error'
                            });
                        }
                    })
            }
        }
    }
</script>

<style scoped lang = "scss">
    #white {
        text-align: center;
        margin-top: 0px;
        background-color: #ffffff;
        min-height: 300px;
        width: 100%;
    }

    .mt {
        margin-top: 50px;
        margin-bottom: 50px;
    }

    .contact-form input, .contact-form textarea {
        padding: 10px 12px;
        border-radius: 0;
        box-shadow: none;
        font-size: 14px;
        height: auto;
    }

    .contact-form .form-send button {
        background: #74c9be;
        border: 0;
        padding: 10px 30px;
        color: #fff;
        transition: 0.4s;
        cursor: pointer;
    }

    .contact-form .form-send button:hover {
        background: #6fdbcd;
    }
</style>